import React from "react";
import "./Result.module.scss";
import ButtonResult from "../ButtonResult/ButtonResult";

class Result extends React.Component {
  render() {
   
    return (
        <section className="result">
        <div className="result__wrapper">
          <div className="result__title"> :</div>
          <div className="result__point">
            <span> </span>
            <span></span>
            <span>,  42</span>
          </div>
          <div className="result__price">
            <span className = "result__price__title">: </span>
            <span className = "result__price__count">  8 000  12 000 </span>
          </div>
          <ButtonResult text = " "/>
        </div>        
      </section>
    );
  }
}
export default Result;
